.subArticleContainer {
  height: 28vw;
  width: 92vw;
  margin: 0 auto;
  position: relative;
  display: flex;
}

.subArticleContainer img {
  position: absolute;
  left: 0;
  top: 6%;
  width: 30%;
  height: 88%;
  border-radius: 20%;
  /* box-shadow: 0 0 4px 1px #ababab; */
}

.subArticleInfo {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  right: 0;
  top: 6%;
  width: 66%;
  height: 88%;
}

.subArticleInfo a {
  text-decoration: none;
}

#title-sub-article {
  font-size: 3.8vw;
  color: #222;
  margin-bottom: 8px;
  font-weight: 700;
}

.othersubHeading {
  font-size: 2.6vw;
}

#ctgType-sub-article {
  color: #5a9bd6;
  text-transform: uppercase;
  font-weight: 300;
  float: left;
}

#date-published-sub-article {
  color: #707070;
  font-weight: 300;
  float: right;
}
