@import url(https://fonts.googleapis.com/css2?family=PT+Sans:wght@700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0 auto;
  font-family: "PT Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  min-width: 100vw;
  min-height: 100vh;
  background: #f2f3f4;
}

.scrollNews {
  margin: 2vh 0 2vh 0;
  /* border: 1px solid black; */
}

.headerContainer {
  position: relative;
  width: 100vw;
  height: 16vh;
  margin-top: 2vh;
  /* border: 1px solid black; */
}

.logo {
  position: absolute;
  display: flex;
  height: 80%;
  left: 0;
  right: 0;
  margin: 0 auto;
  /* border: 1px solid black; */
}

.headerContainer img {
  height: 100%;
  max-width: 100%;
  position: absolute;
  align-self: center;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 100%;
}

.curDay {
  position: absolute;
  display: flex;
  /* height: 20%; */
  right: 0;
  bottom: 0;
  margin-right: 6vw;
  /* border: 1px solid black; */
}

.curDay h4 {
  font-size: 3.2vw;
  font-weight: 300;
  color: #707070;
}

.scrollNews {
  display: flex;
  overflow-x: scroll;
  -ms-scroll-snap-type: x mandatory;
      scroll-snap-type: x mandatory;
}

.articleContainer {
  min-width: 60vw;
  height: 60vw;
  border-radius: 16%;
  margin-left: 4vw;
  scroll-snap-align: center;
  position: relative;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0 0 4px 1px #ababab;
}

.articleInfo {
  width: 88%;
  border-radius: 20px;
  padding: 6% 8%;
  position: absolute;
  background-color: white;
  margin: 8% auto;
  bottom: 0;
  right: 0;
  left: 0;
}

.articleInfo a {
  text-decoration: none;
}

#title {
  font-size: 3.8vw;
  color: #222;
  font-weight: 700;
}

.subHeading {
  width: 100%;
  display: inline-block;
  margin: 1vh 0 -4px 0;
  font-size: 2.6vw;
}

#ctgType {
  color: #5a9bd6;
  text-transform: uppercase;
  font-weight: 300;
  float: left;
}

#datePublished {
  color: #707070;
  font-weight: 300;
  float: right;
}

.subArticleContainer {
  height: 28vw;
  width: 92vw;
  margin: 0 auto;
  position: relative;
  display: flex;
}

.subArticleContainer img {
  position: absolute;
  left: 0;
  top: 6%;
  width: 30%;
  height: 88%;
  border-radius: 20%;
  /* box-shadow: 0 0 4px 1px #ababab; */
}

.subArticleInfo {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  right: 0;
  top: 6%;
  width: 66%;
  height: 88%;
}

.subArticleInfo a {
  text-decoration: none;
}

#title-sub-article {
  font-size: 3.8vw;
  color: #222;
  margin-bottom: 8px;
  font-weight: 700;
}

.othersubHeading {
  font-size: 2.6vw;
}

#ctgType-sub-article {
  color: #5a9bd6;
  text-transform: uppercase;
  font-weight: 300;
  float: left;
}

#date-published-sub-article {
  color: #707070;
  font-weight: 300;
  float: right;
}

