.scrollNews {
  display: flex;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}

.articleContainer {
  min-width: 60vw;
  height: 60vw;
  border-radius: 16%;
  margin-left: 4vw;
  scroll-snap-align: center;
  position: relative;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0 0 4px 1px #ababab;
}

.articleInfo {
  width: 88%;
  border-radius: 20px;
  padding: 6% 8%;
  position: absolute;
  background-color: white;
  margin: 8% auto;
  bottom: 0;
  right: 0;
  left: 0;
}

.articleInfo a {
  text-decoration: none;
}

#title {
  font-size: 3.8vw;
  color: #222;
  font-weight: 700;
}

.subHeading {
  width: 100%;
  display: inline-block;
  margin: 1vh 0 -4px 0;
  font-size: 2.6vw;
}

#ctgType {
  color: #5a9bd6;
  text-transform: uppercase;
  font-weight: 300;
  float: left;
}

#datePublished {
  color: #707070;
  font-weight: 300;
  float: right;
}
