.headerContainer {
  position: relative;
  width: 100vw;
  height: 16vh;
  margin-top: 2vh;
  /* border: 1px solid black; */
}

.logo {
  position: absolute;
  display: flex;
  height: 80%;
  left: 0;
  right: 0;
  margin: 0 auto;
  /* border: 1px solid black; */
}

.headerContainer img {
  height: 100%;
  max-width: 100%;
  position: absolute;
  align-self: center;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 100%;
}

.curDay {
  position: absolute;
  display: flex;
  /* height: 20%; */
  right: 0;
  bottom: 0;
  margin-right: 6vw;
  /* border: 1px solid black; */
}

.curDay h4 {
  font-size: 3.2vw;
  font-weight: 300;
  color: #707070;
}
